import React, { useState } from "react";
import { Link } from 'react-router-dom';
import logo from "../../assets/images/Home/wcslogo.png";
const Header = () => {
  const [colorChange, setColorchange] = useState(false);
  const [isCollapsed, setIsCollapsed] = useState(true);


  const scrollToElementClicked = (event) => {
    if (window.innerWidth < 850) {
      isCollapsed = setIsCollapsed(!isCollapsed);
    }
    var x = document.getElementById(event);
    x.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
  }
  const changeNavbarColor = () => {
    if (window.scrollY >= 80) {
      setColorchange(true);
    }
    else {
      setColorchange(false);
    }
  };

  window.addEventListener('scroll', changeNavbarColor);
  return (
    <>
      <nav id="nav" class={`${colorChange && 'navbar-bg-white react'} navbar fixed-top scrolled navbar-expand-lg`}>
        <div class="container px-0">
          <a class="navbar-brand position-relative">
            <Link to="/"><img loading="lazy" class="img-fluid"
              src={logo} alt="Quick Lending" title="Quick Lending"
              style={{ "max-width": "160px" }} /></Link>
          </a>
          <button class="navbar-toggler" type="button" data-toggle="collapse"
            data-target="#collapsibleNavbar">
            <span style={{ "color": "#213e7a" }} class="navbar-toggler-icon fa fa-bars"></span>
          </button>
          <div class="collapse navbar-collapse" id="collapsibleNavbar">
            <div class="navbar-collapse-header">

            </div>
            <nav role="navigation" class="nav_menu w-nav-menu ml-auto">
              <div class="nav_menu-content">
                <div class="nav_link-list">
                  <a href="javascript:void(0)"
                    class="nav_link w-nav-link" onClick={() => scrollToElementClicked('about')}>About Us</a>
                  <a href="javascript:void(0)"
                    class="nav_link w-nav-link" onClick={() => scrollToElementClicked('who_we_are')}>Services</a>
                  <a href="javascript:void(0)"
                    class="nav_link w-nav-link" onClick={() => scrollToElementClicked('faq')}>FAQ</a>
                     <a href="javascript:void(0)"
                    class="nav_link w-nav-link" onClick={() => scrollToElementClicked('our_team')}>Our Team</a>
                  <a href="javascript:void(0)"
                    class="nav_link w-nav-link" onClick={() => scrollToElementClicked('contact_us')}>Contact Us</a>
                  {/* <a class="nav_link w-nav-link">Get Estimate</a> */}
                </div>
                <div class="nav_button-row">
                  <Link to={'/login'} class="button-outline is-nav button-nav w-button">Borrower
                    Portal</Link>
                </div >
              </div >
            </nav >
          </div >
        </div >
      </nav >
    </>
  )
}
export default Header