import React, { useState } from "react";
import './Signup.scss'
import closeEye from '../../assets/images/application/close-eye.png'
import logo from '../../assets/images/application/WCS_PS_Logo.png'
import { ErrorMessage, Formik, Form } from "formik";
import { Link } from "react-router-dom";
import * as yup from 'yup'

const SignupPage = () => {
    const [showPassword, setShowPassword] = useState(false)
    const [showConfirmPassword, setShowConfirmPassword] = useState(false)
    const defaultvalues = {
        first_name: '',
        last_name: '',
        email: '',
        phone_number: '',
        password: '',
        confirm_password: ''
    }
    const validationSchema = yup.object().shape({
        first_name: yup.string().required('first name required'),
        last_name: yup.string().required('last name required'),
        email: yup.string().required('email address required').email('enter valid email address'),
        phone_number: yup.string().required('phone number required').max(10, 'invalid phone number').min(10, 'invalid phone number'),
        password: yup.string().required('password required'),
        confirm_password: yup.string().oneOf([yup.ref('password')], 'passwords must match')
    })
    const handleSubmit = (value, { resetForm }) => {
        console.log(value)
        resetForm()
    }
    return (
        <>
            <div className="container-fluid apply-online-process-beta">
                <div className="main-beta responsivemain-beta">
                    <Link to={'/'} className="navbar-brand position-relative">
                        <img src={logo} className="navbar-brand-img"
                            style={{ display: 'block !important', width: '150px' }} />
                    </Link>
                    <div className="main-form">
                        <div id="msform" className="card msform-bgwhiteresponsive maxwidth-custome">

                            <div className="mb-5 text-center">
                                <h3 className="login-title-wlcm mt-2 mb-0">WCS Property Solutions</h3>
                                <p className="login-subtitle">Please enter your details to continue.</p>
                            </div>
                            <Formik
                                initialValues={defaultvalues}
                                validationSchema={validationSchema}
                                onSubmit={handleSubmit}>
                                {({ values, handleChange, handleBlur, isSubmitting, submitCount, setFieldValue }) => (
                                    <Form>
                                        <div className="mt-3 mb-4 row form-group">
                                            <div className="col-md-6 ">
                                                <div>
                                                    <label for="first_name" className="input-label text-left">First Name *</label>
                                                </div>
                                                <input
                                                    name="first_name"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your first name"
                                                    type="text"
                                                    value={values.first_name}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='first_name' component='div' />
                                            </div>
                                            <div className="col-md-6">
                                                <div>
                                                    <label for="last_name" className="input-label text-left">Last Name *</label>
                                                </div>
                                                <input
                                                    name="last_name"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your last name"
                                                    type="text"
                                                    value={values.last_name}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='last_name' component='div' />
                                            </div>
                                        </div>
                                        
                                        <div className="mt-3 mb-4 form-group row">
                                            <div className="col-md-6">
                                                <div>
                                                    <label for="email" className="input-label text-left">Email Address *</label>
                                                </div>
                                                <input
                                                    name="email"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your Email Address"
                                                    type="email"
                                                    value={values.email}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='email' component='div' />
                                            </div>
                                            <div className="col-md-6">
                                                <div>
                                                    <label for="phone_number" className="input-label text-left">Phone Number *</label>
                                                </div>
                                                <input
                                                    name="phone_number"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your phone number"
                                                    type="text"
                                                    value={values.phone_number}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='phone_number' component='div' />
                                            </div>
                                        </div>
                                       
                                        <div className="mt-3 form-group row">
                                            <div className="position-relative col-md-6">
                                                <div>
                                                    <label for="password" className="input-label text-left">Password *</label>
                                                </div>
                                                <input
                                                    type={showPassword ? 'text' : 'password'}
                                                    name="password"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your password"
                                                    value={values.password}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='password' component='div' />
                                                <div className="icon-eye-position">
                                                    {showPassword ?
                                                        <span><img src={closeEye}
                                                            alt="close_eye" style={{ height: '23px' }} onClick={() => setShowPassword(!showPassword)} />
                                                        </span>
                                                        :
                                                        <span><svg xmlns="http://www.w3.org/2000/svg"
                                                            width="21" height="18" fill="#A6AEBE" className="bi bi-eye" viewBox="0 0 16 16" onClick={() => setShowPassword(!showPassword)}>
                                                            <path
                                                                d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8zM1.173 8a13.133 13.133 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5c2.12 0 3.879 1.168 5.168 2.457A13.133 13.133 0 0 1 14.828 8c-.058.087-.122.183-.195.288-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5c-2.12 0-3.879-1.168-5.168-2.457A13.134 13.134 0 0 1 1.172 8z" />
                                                            <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5zM4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0z" />
                                                        </svg></span>
                                                    }
                                                </div>

                                            </div>
                                            <div className="position-relative col-md-6">
                                                <div>
                                                    <label for="confirm_password" className="input-label text-left">Confirm Password *</label>
                                                </div>
                                                <input
                                                    type={showConfirmPassword ? 'text' : 'password'}
                                                    name="confirm_password"
                                                    className="form-control input-custome-login"
                                                    placeholder="Enter your confirm password"
                                                    value={values.confirm_password}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />
                                                <ErrorMessage className="validation-error" name='confirm_password' component='div' />
                                                <div className="icon-eye-position">
                                                    {showConfirmPassword ?
                                                        <span><img src={closeEye}
                                                            alt="close_eye" style={{ height: '23px' }} onClick={() => setShowConfirmPassword(!showConfirmPassword)} />
                                                        </span>
                                                        :
                                                        <span><svg xmlns="http://www.w3.org/2000/svg"
                                                            width="21" height="18" fill="#A6AEBE" className="bi bi-eye" viewBox="0 0 16 16" onClick={() => setShowConfirmPassword(!showConfirmPassword)}>
                                                            <path
                                                                d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8zM1.173 8a13.133 13.133 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5c2.12 0 3.879 1.168 5.168 2.457A13.133 13.133 0 0 1 14.828 8c-.058.087-.122.183-.195.288-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5c-2.12 0-3.879-1.168-5.168-2.457A13.134 13.134 0 0 1 1.172 8z" />
                                                            <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5zM4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0z" />
                                                        </svg></span>
                                                    }
                                                </div>

                                            </div>
                                        </div>
                                       
                                        <div className="mt-3">
                                            <button type="submit" className="px-5 btn btn w-100 text-white" style={{ backgroundColor: '#d9b451' }}>Sign Up</button>
                                        </div>
                                        <div className="text-right">
                                            <a className="mx-1 reset" style={{ color: '#2C3E50' }} href="javascript:void(0)"
                                                tooltip="Reset Password" placement="top">
                                                <span className=" btn-inner--text" style={{ fontSize: '12px' }}>Already have account? <Link to='/login'>Sign In</Link></span>
                                            </a>
                                        </div>
                                    </Form>
                                )}
                            </Formik>
                        </div>
                    </div>

                </div>
            </div>
        </>
    )
}
export default SignupPage