import React,{useState} from "react";
import foterlogo from '../../assets/images/Home/WCS_PS_Logo-social.jpg';
const Footer = ()=>{
    const [isCollapsed, setIsCollapsed] = useState(true)
    const scrollToElementClicked = (event) => {
        if (window.innerWidth < 850) {
          isCollapsed = setIsCollapsed(!isCollapsed);
        }
        var x = document.getElementById(event);
        x.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
      } 
      
    return(
        <>
              <footer class="section-bottom-footer background-color-1">
                  <div class="page-padding">
                    <div class="container-width-large">
                      <div class="padding-vertical padding-xxlarge">
                        <div class="row">
                          <div class="col-lg-3 pt-4 pt-lg-0">
                            <div class="footer_logo-container">
                              <a href="#" aria-current="page" class="brand brand-footer w-inline-block w--current">
                                <img src={foterlogo} alt="Quicklending"
                                  style={{ "maxWidth": "220px" }} title="QuickLendingLogo-NoTagline" class="img-fluid" />
                              </a>
                              <p class="footer-logo-b-text" style={{ "maxWidth": "20em" }}>
                                {/* Were a Houston area Hard Money Lender focused on helping
                                you close your loan quickly and smoothly. Our talented and
                                diverse team will guide you through the loan process so you
                                always know what is happening with your loan. */}
                              </p>
                            </div>
                          </div>
                          <div class="col-lg-9">
                            <div class="row">
                              <div class="col-lg-4 pt-4 pt-lg-0">
                                <div class="mb-3">
                                  <h6 class="text-color-white" style={{
                                    "borderBottom": "0.5px solid #cccccc",
                                    "fontWeight": 600, "fontSize": "18px"
                                  }}>
                                    WCS Property Solutions
                                  </h6>
                                </div>
                                <div class="footer_link-wrapper">
                                  <a href="tel:+17204969579" class="footer-link"><i class="fa fa-mobile-alt"></i> &nbsp; (346) 496-9579</a>
                                  <a href="mailto:mauricesims@wcspropertyslutions.com" class="footer-link"><i class="fa fa-envelope"></i> &nbsp;
                                  mauricesims@wcspropertyslutions.com</a>
                                  <a target="_blank" href="https://goo.gl/maps/ciyo4FfLUxAMUc6a8" class="footer-link"><i
                                    class="fa fa-map"></i> &nbsp; P.O. Box 201912 Denver, CO 80220</a>
                                </div>
                              </div>
                              <div class="col-lg-4 pt-4 pt-lg-0">
                                <div class="mb-3">
                                  <h6 class="text-color-white" style={{ "borderBottom": "0.5px solid #cccccc", "fontWeight": 600, "fontSize": "18px" }}>
                                    Website
                                  </h6>
                                </div>
                                <div class="footer_link-wrapper">
                                  <a class="footer-link" onClick={() => scrollToElementClicked('about')}>About Us</a>
                                  <a class="footer-link" onClick={() => scrollToElementClicked('who_we_are')}>Services</a>
                                  <a class="footer-link" onClick={() => scrollToElementClicked('faq')}>FAQ</a>
                                  <a class="footer-link" onClick={() => scrollToElementClicked('contact_us')}>Contact Us</a>
                                </div>
                              </div>
                              <div class="col-lg-4 pt-4 pt-lg-0">
                                <div class="mb-3">
                                  <h6 class="text-color-white" style={{ "borderBottom": "0.5px solid #cccccc", "fontWeight": 600, "fontSize": "18px" }}>
                                    Service Areas
                                  </h6>
                                </div>
                                <div class="footer_link-wrapper">
                                  <a class="footer-link">Cypress, TX</a>
                                  <a class="footer-link">Houston, TX</a>
                                  <a class="footer-link">Katy, TX</a>
                                  <a class="footer-link">Missouri City, TX</a>
                                  <a class="footer-link">Sugar Land, TX</a>
                                  <a class="footer-link">Pearland, TX</a>
                                  <a class="footer-link">Pasadena, TX</a>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </footer>
        </>
    )
}
export default Footer